nav {
    position: fixed;
    font-family: 'PT Sans', sans-serif;
    background-color: rgb(221, 37, 37);
}
nav .navbar-brand {
    font-size: 26px;
    padding: 10px;
}
nav ul li a {
    color: white;
    padding: 20px;
    font-size: 18px;
    text-decoration: none;
    font-weight: 700;
    margin: 5px;
}
nav ul li a .fa {
    font-size: 24px;
}
nav a {
    margin: 5px;
    padding: 5px;
}
/* footer navs */
#footerList {
    margin-top: 20px;
    padding: 10px;
    font-size: 14px;
    border-bottom: 6px solid orange;
 } 
 


/* desktop interface */
@media (max-width: 986px) {
    nav ul li a {
        color: white;
        padding: 20px;
        font-size: 16px;
        text-decoration: none;
        font-weight: normal;
        margin: 5px;
    }
    #text-nav {
        font-size: 22px;
    }
    #navbarText {
        margin: 20px;
    }
    nav .navbar-brand {
        margin: 10px;
    }
    .navbar {
        padding: 0;
    }
    #footerList {
        margin-top: 20px;
        padding: 10px;
        font-size: 14px;
        display: flex;
     } 
}